.introduce {
  height: 1000px;
  background-color: black;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}

.introduce > * {
  margin: 50px 20px;
  height: 400px;
  width: 600px;
}

#introduce-words-1 {
  color: #ffffdf;
  font-size: 2em;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

#introduce-words-1 * {
  transform: translateX(13%);
  margin: 0;
  z-index: 2;
}
#introduce-words-1 > h1 {
  font-weight: 700;
  margin: 5px 0;
}
#introduce-words-1 > h4::before {
  display: block;
  content: "";
  width: 100px;
  height: 100px;
  background-color: purple;
  position: relative;
  z-index: -1;
  top: 20%;
  border-radius: 20% 20% 50% 50%;
  box-shadow: 30px 30px purple;
  filter: blur(80px);
}
#introduce-words-1 > h6 {
  line-height: 1.4em;
}
#introduce-words-1 > h6::after {
  display: block;
  z-index: -1;
  content: "";
  width: 100px;
  height: 100px;
  background-color: green;
  position: relative;
  left: 40%;
  bottom: 40%;
  filter: blur(80px);
  box-shadow: 30px 30px green;
  border-radius: 20% 50% 50% 50%;
}

#introduce-gif-1 > img {
  width: 100%;
  height: 100%;
}
#introduce-gif-1 {
  position: relative;
}
#introduce-gif-1:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  box-shadow: inset 40px 40px 50px black;
}
#introduce-gif-1:before {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  content: "";
  box-shadow: inset -40px -40px 50px black;
}
#introduce-gif-2 > img {
  width: 100%;
  height: 100%;
}

#introduce-words-2 {
  color: #ffffdf;
  line-height: 1.2em;
  font-size: 2em;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
